import React, { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import CloseIcon from '@mui/icons-material/Close';
import { checkStatus } from 'app/constant';
import MaterialTable from 'material-table';
import { getListCandidate } from './EmployeeService';
import { toast } from 'react-toastify';
import EmployeeDialog from './EmployeeDialog';
import { colorTable } from 'app/constant';
export default function CandidateTable(props) {
  const { open, handleClose } = props;
  const [listCandidate, setListCandidate] = useState([]);
  const [shouldOpenEmployeeDialog, setShouldOpenEmployeeDialog] = useState();
  const [item, setItem] = useState({});

  const columns = [
    {
      title: 'STT',
      field: 'STT',
      render: (rowData) => rowData.tableData.id + 1,
      cellStyle: {
        width: '3%',
        textAlign: 'center',
      },
    },
    {
      title: 'Thao tc',
      field: 'action',
      render: (rowData) => (
        <>
          <IconButton
            color="primary"
            onClick={() => {
              setItem(rowData);
              setShouldOpenEmployeeDialog(true);
            }}
          >
            <RemoveRedEyeIcon />
          </IconButton>
        </>
      ),
      cellStyle: {
        width: '10%',
        textAlign: 'center',
      },
    },
    {
      title: 'M h s',
      field: 'code',
      render: (rowData) => rowData?.code,
      cellStyle: {
        width: '10%',
        textAlign: 'center',
      },
    },
    {
      title: 'H v tn',
      field: 'fullName',
      render: (rowData) => rowData?.fullName,
      cellStyle: {
        width: '10%',
        textAlign: 'left',
      },
      headerStyle: {
        textAlign: 'left',
      },
    },
    {
      title: 'Email',
      field: 'email',
      render: (rowData) => rowData?.email,
      cellStyle: {
        width: '10%',
        textAlign: 'left',
      },
      headerStyle: {
        textAlign: 'left',
      },
    },
    {
      title: 'ST',
      field: 'phone',
      render: (rowData) => rowData?.phone,
      cellStyle: {
        width: '10%',
        textAlign: 'left',
      },
      headerStyle: {
        textAlign: 'left',
      },
    },
    {
      title: 'Trng thi',
      field: 'status',
      render: (rowData) => {
        let message = checkStatus(rowData.status).message;
        let color = checkStatus(rowData.status).color;
        return <div className={color}>{message}</div>;
      },
      cellStyle: {
        width: '10%',
        textAlign: 'left',
      },
      headerStyle: {
        textAlign: 'left',
      },
    },
  ];

  useEffect(() => {
    updatePageData();
  }, []);

  const updatePageData = () => {
    getListCandidate()
      .then((res) => {
        if (res.data.statusCode === 200) {
          setListCandidate(res.data.data.filter((item) => item.status === 8));
        } else {
          toast.warning('Li xc thc!');
        }
      })
      .catch((err) => toast.error('C li xy ra!'));
  };

  return (
    <>
      <Dialog open={open} fullWidth maxWidth={'lg'}>
        <DialogTitle>
          <Box className="icon-close" onClick={handleClose}>
            <IconButton color="error">
              <CloseIcon />
            </IconButton>
          </Box>
        </DialogTitle>
        <DialogContent style={{ padding: '30px 20px 0' }}>
          <MaterialTable
            title="Danh sch h s ng vin"
            columns={columns}
            data={listCandidate}
            options={{
              sorting: false,
              draggable: false,
              maxBodyHeight: '40vh',
              pageSize: 10,
              pageSizeOptions: [10, 20, 50],
              headerStyle: {
                textAlign: 'center',
                backgroundColor: colorTable.HEADER,
                color: colorTable.TEXTHEADER,
              },
              rowStyle: {
                backgroundColor: colorTable.ROW,
                color: colorTable.TEXTROW,
              },
            }}
            localization={{
              toolbar: {
                searchTooltip: 'Tm kim',
                searchPlaceholder: 'Tm kim',
              },
              pagination: {
                labelDisplayedRows: '{from}-{to} ca {count}',
                labelRowsSelect: 'hng',
                labelRowsPerPage: 'S hng mi trang:',
                firstAriaLabel: 'Trang u',
                firstTooltip: 'Trang u',
                previousAriaLabel: 'Trang trc',
                previousTooltip: 'Trang trc',
                nextAriaLabel: 'Trang sau',
                nextTooltip: 'Trang sau',
                lastAriaLabel: 'Trang cui',
                lastTooltip: 'Trang cui',
              },
              body: { emptyDataSourceMessage: 'Khng c bn ghi no' },
            }}
          />
        </DialogContent>
        <DialogActions>
          <Button variant="contained" color="secondary" onClick={handleClose}>
            Hy
          </Button>
        </DialogActions>
      </Dialog>
      {shouldOpenEmployeeDialog && (
        <EmployeeDialog
          open={shouldOpenEmployeeDialog}
          handleCloseDialog={() => setShouldOpenEmployeeDialog(false)}
          handleClose={handleClose}
          candidate={item}
        />
      )}
    </>
  );
}
